<template lang="html">
  <header>
    <h3><a href="#" :style="{backgroundImage: bg}">蒙荣文化</a></h3>
    <h2 @click="toggle"><a class="mobile-menu" ><i class="fa fa-navicon fa-lg fa-fw"></i>MENU</a></h2>
    <nav>
      <ul>
        <li @click="up"><router-link to="/about" active-class="selected"><i class="fa fa-at fa-fw"></i>关于我们</router-link></li>
        <li @click="up"><router-link to="/works" active-class="selected"><i class="fa fa-diamond fa-fw"></i>案例与荣誉</router-link></li>
        <li @click="up"><router-link to="/design" active-class="selected"><i class="fa fa-ravelry fa-fw"></i>设计与插画</router-link></li>
        <li @click="up"><router-link to="/join" active-class="selected"><i class="fa fa-wpexplorer fa-fw"></i>招聘</router-link></li>
        <li @click="up"><router-link to="/contact" active-class="selected"><i class="fa fa-paper-plane-o fa-fw"></i>联系我们</router-link></li>
      </ul>
    </nav>
  </header>
</template>

<script>

export default {
  name: 'header',
  computed: {
    bg: function(){
      return 'url(../mr/static/logo.png)'
    }
  },
  methods: {
    up(){
      this.$el.querySelector('nav').classList.remove('expend')
      this.$el.classList.remove('overflow')
    },
    toggle(){
      this.$el.classList.toggle('overflow')
      this.$el.querySelector('nav').classList.toggle('expend')
    }
  }
}
</script>

<style lang="scss" scoped>
$active-color: rgb(14, 209, 115);
$hover-color: rgb(29, 208, 95);
$logo-color: #fff;
$font-color: #333;
$li-color: #e6e6e6;

.selected{
  margin-top: -10px;
  line-height: 60px;
  height: 60px;
  background-color: $active-color;
  &:hover{
    background-color: $active-color;
  }
}

.expend{
  height: 50px;
  bottom: -50px;
  z-index: 200;
}
.overflow{
  overflow: visible;
}
h2{
  display: none;
}

header{
  height: 65px;
  overflow: hidden;
  position: relative;
  width: 100%;
  min-width: 1000px;
  max-width: 1500px;
  margin: 0 auto;
}
h3{
  width: 16.6667%;
  height: 100%;
  z-index: 201;
  background-color: $logo-color;
  position: relative;
  a{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -75px;
    margin-top: -30px;
    width: 150px;
    height: 60px;
    background:  no-repeat center;
    background-size: contain;
    text-indent: -9999px;
  }
}
nav{
  width: 83.3333%;
  height: 50px;
  margin-left: 16.66667%;
  position: absolute;
  bottom: 0;
  transition: all 0.3s linear;
}

@media (max-width: 786px) {
  header{
    min-width: inherit;
  }
  h3{
    width: 60%;
    display: inline-block;
    float: left;
  }
  h2{
    display: flex;
    width: 40%;
    height: 100%;
    position: relative;
    z-index: 201;
    background-color: $logo-color;
    justify-content: center;
    a{
      align-self: center;
    }
  }
  nav{
    height: 50px;
    overflow: visible;
    bottom: 250px;
    width: 100%;
    margin-left: 0;
  }
}

ul{
  width: 100%;
  height: 100%;
}
li{
  float: left;
  width: 20%;
  height: 100%;
  a{
    display: inline-block;
    height: 100%;
    width: 100%;
    line-height: 50px;
    color: $font-color;
    font-size: 14px;
    text-align: center;
    background-color: $li-color;
    position: relative;
    border-left: 1px solid white;
    border-bottom: 1px solid white;
    transition: all 0.3s ease-out, line-height 0.1s ease-out;
    &:hover{
      background-color: $hover-color;
      color: #fff;
    }
    &:first-child{
      border-left: 1px solid $li-color;
    }
    i{
      margin-right: 0.5em;
      vertical-align: baseline;
    }
  }
}
@media (max-width: 768px) {
  li{
    width: 100%;
  }
}
</style>



