<template lang="html">
  <section class="list">
    <div class="container">
      <ul>
        <router-link tag="li" :to="{ name: 'worksinfo', params: { id: 'ejn' }}"><img src="../mr/static/ejn/banner.jpg" alt=""><p>额济纳有礼</p></router-link>
        <router-link tag="li" :to="{ name: 'worksinfo', params: { id: 'als' }}"><img src="../mr/static/als/banner.jpg" alt=""><p>阿拉善好礼</p></router-link>
        <router-link tag="li" :to="{ name: 'worksinfo', params: { id: 'dmcg' }}"><img src="../mr/static/dmcg/banner.jpg" alt=""><p>大漠长歌系列围巾</p></router-link>

        <router-link tag="li" :to="{ name: 'worksinfo', params: { id: 'zld' }}"><img src="../mr/static/zld/banner.jpg" alt=""><p>众联达 Logo 设计</p></router-link>
        <router-link tag="li" :to="{ name: 'worksinfo', params: { id: 'hunu' }}"><img src="../mr/static/hunu/banner.jpg" alt=""><p>HunNu 品牌设计</p></router-link>

      </ul>
    </div>
  </section>
</template>

<script>
export default {
  name: 'WorksList'
}
</script>

<style lang="scss" scoped>
.list{
  background-image: radial-gradient(circle, #f9f9f9,#4290ff 10%, #0d1519);
  height: 100%;
  width: 100%;
  overflow-y: auto;
  // background: url(../mr/static/bg.jpg) no-repeat center;
  // background-size: cover;
  .container{
    padding: 30px 20px;
  }
}
ul{
  width: 100%;
  // display: flex;
  // justify-content: space-around;
  // flex-flow: wrap;
  li{
    // background: tomato;
    width: 26%;
    margin-left: 6%;
    display: inline-block;
    vertical-align: top;
    margin-top: 10px;
    color: white;
    text-align: left;
    opacity: 0.8;
    &:hover img{
      outline-color: #4caf50;
    }
    // filter: blur(0.8px);
    img{
      width: 100%;
      max-width: 104px;
      max-height: 58px;
      outline: 4px solid #fff;
      box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.39);
      transition: all 0.3s ease-out;
    }
    p{
      line-height: 1.5em;
      margin-top: 5px;
      font-size: 14px;
      text-indent: -4px;
    }
  }
}
.router-link-active{
  opacity: 1;
  filter: blur(0px);
  img{
    outline-color: #4caf50;
  }
}
</style>



