<template lang="html">
  <div class="page-404">
    <figure>
      <img src="../mr/static/404.jpg" alt="NotFound">
      <figcaption>
        <h3>你要访问的页面已经挂掉了</h3>
      </figcaption>
    </figure>
  </div>
</template>

<script>
export default {
  name: 'not-found-component'
}
</script>

<style lang="css" scoped>
  .page-404{
    width: 100%;
    min-width: 320px;
    /*background-color: #fff;*/
    border-radius: 5px;
    /*background: url(../mr/static/bg.jpg) no-repeat center;*/
    background-size: cover;
  }
  figure{
    width: 100%;
    margin: 0;
  }
  h3{
    font-size: 22px;
    color: #333;
    letter-spacing: 0.011em;
  }
  img{
    width: 100%;
  }
  figcaption{
    padding: 16px;
  }
</style>



