<template lang="html">
  <div class="home">
    <div class="top-side">
      <div class="side-wrap">
        <div class="stack triple">
          <div class="square square-2">
            <div class="block">
              <gallery class="gallery"></gallery>
            </div>
          </div>
          <div class="square">
            <div class="block">
              <brand class="brand"></brand>
            </div>
          </div>
        </div>
        <div class="stack double">
          <div class="square square-75">
            <div class="block block-border">
              <about class="about"></about>
            </div>
          </div>
          <div class="square square-50 top">
            <div class="block block-border">
              <create></create>
            </div>
          </div>
        </div>
        <div class="stack">
          <div class="square-113 square">
            <div class="block block-border">
              <contact></contact>
            </div>
          </div>
          <div class="square square-113">
            <div class="block block-border">
              <illusion class="illusion" bg="url(../mr/static/right-2.jpg)" size="100%"></illusion>
            </div>
          </div>
        </div>
      </div>
    </div>
    <footer-component></footer-component>
    </div>
</template>

<script>
import Gallery from '../components/Gallery'
import About from '../components/About'
import Illusion from '../components/Illusion'
import Brand from '../components/Brand'
import Contact from '../components/Contact'
import FooterComponent from '../components/Footer'
import Create from '../components/Create'

export default {
  name: 'home',
  components: {
    Gallery,
    About,
    Illusion,
    Brand,
    Contact,
    FooterComponent,
    Create
  }
}
</script>

<style lang="scss" scoped>
.tab{
  width: 50%;
  float: left;
}
.tab-2{
  width: 33.334%;
  float: left;
}
.gallery{
  width: 100%;
  height: 100%;
  float: left;
}
.home {
  width: 100%;
  max-width: 1500px;
  min-width: 1000px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  .top-side{
    padding: 0 0 37.5% 0;
    position: relative;
    .side-wrap{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
  }
}

.stack{
  width: 16.666%;
  float: left;
  display: block;
  position: relative;
  vertical-align: top;
  // border: 1px solid #000;
}
.triple{
  width: 50%;
}
.double{
  width: 33.334%;
}
@media (max-width: 768px) {
  .home{
    overflow: visible;
    min-width: inherit;
    .top-side{
      padding-bottom: 307%;
    }
  }
  .stack{
      width: 100%;
  }
  .triple{
    width: 100%;
  }
  .double{
    width: 100%;
  }
}
.square{
  display: block;
  position: relative;
  padding: 0 0 25% 0;
  .block{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
  }
  .block-border{
    left: 1px;
  }
}
.square-2{
  padding: 0 0 50% 0;
}
.square-50{
  padding: 0 0 48% 0;
}
.top{
  margin-top: -2px;
}
.square-75{
  padding: 0 0 65.5% 0;
}
.square-100{
  padding: 0 0 300% 0;
}
.square-113{
  padding-bottom: 113%;
}
@media (max-width: 768px) {
  .square-113{
    padding-bottom: 60%;
  }
  .square .block{
    border-left: 0;
  }
  .square .block-border{
    left: 0;
  }
}
</style>



