    <span class="title">我们提供的服务</span>
    <div class="list">
      <dl>

        <span>
        <dt>
          <span>
            <img src="../mr/static/design.png" alt="">
            文旅产品设计
          </span>
        </dt>
        <dd>深度挖掘文化内核，融合文化精神与产品内涵</dd>
        </span>

        <span>
        <dt>
          <span>
            <img src="../mr/static/wechat.png" alt="">
            公众号代理营销
          </span>
        </dt>
        <dd>为企业打造一流媒体平台，助力企业产品推广与业务宣传</dd>
        </span>

        <span>
        <dt>
          <span>
            <img src="../mr/static/world.png" alt="">
            国际文化交流
          </span>
        </dt>
        <dd>为各类组织机构策划提供国际间的文化交流活动</dd>
        </span>

        <span>
        <dt>
          <span>
            <img src="../mr/static/web.png" alt="">
            品牌网站创意
          </span>
        </dt>
        <dd>为企业品牌或产品量身打造的网站创意制作与运营维护服务</dd>
