<script>
export default {
  name: 'Service'
}
</script>

<style lang="scss" scoped>
.service{
  width: 100%;
  height: 100%;
  .title{
    font-weight: bold;
    color: #000;
    font-size: 14px;
    position: absolute;
    top: 6%;
    margin-left: 9%;
  }
  .list{
    margin-left: 10%;
    width: 80%;
    position: absolute;
    top: 12%;
    line-height: 18px;
  }
}

dl>span{
  display: block;
  padding: 4% 0;
}
dt{
  float: left;
  color: #666666;
  width: 38%;
  span{
    color: #000;
    padding: 0 24px 0 0;
    width: 120px;
    display: inline-block;
    font-size: 12px;
    img{
      float: left;
      padding: 0 12px 0 0;
    }
  }
}
dd{
  font-size: 12px;
  border-bottom: 1px dashed #fafafa;
  letter-spacing: 0.5px;
  margin-left: 2px;
  padding-left: 38%;
  &:last-child{
    border: 0;
  }
}
img{
  width: 42px;
}
</style>



