<template lang="html">
  <aside class="illusion">
    <div :style="{backgroundImage: bg, backgroundSize: size}">

    </div>
  </aside>
</template>

<script>
export default {
  name: 'illusion',
  props: {
    bg: {
      type: String,
      default: function(){
        return 'url(../mr/static/about-left.jpg)'
      }
    },
    size: {
      type: String
    }
  },
  computed: {
    random(){
      var random =  Math.floor(Math.random(this.imgs.length))
      return this.imgs[random]
    }
  }
}
</script>

<style lang="scss" >
.illusion{
  width: 100%;
  float: right;
  height: 600px;
  div{
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
  }
}
</style>



