<template lang="html">
  <div class="swipe">
    <swiper :options="swiperOption" ref="mySwiper2" class="bg-div" :style="{height: height}">
      <swiper-slide v-for="(i,index) in imgs" :key="i"><img :src="i" alt="" ></swiper-slide>

      <!-- <div class="swiper-pagination"  slot="pagination"></div> -->
      <div class="swiper-button-prev" slot="button-prev"></div>
      <div class="swiper-button-next" slot="button-next"></div>
    </swiper>
  </div>
</template>

<script>
import {brand} from '../api/api'

export default {
  name: 'Brand',
  data() {
      return {
        swiperOption: {
          control: 'mySwiper2',
          notNextTick: true,
          paginationClickable: true,
          slidesPerView: 5,
          spaceBetween: 30,
          loop: false,
          effect : 'slide',
          direction : this.direction,
          grabCursor : true,
          setWrapperSize :true,
          autoHeight: false,
          // pagination : '.swiper-pagination',
          paginationClickable :true,
          prevButton:'.swiper-button-prev',
          nextButton:'.swiper-button-next',
          // scrollbar:'.swiper-scrollbar',
          mousewheelControl : true,
          observeParents:true,
          debugger: true,
          // swiper callbacks
          // swiper的各种回调函数也可以出现在这个对象中，和swiper官方一样
          // onTransitionStart(swiper){
          //   console.log(swiper)
          // },
        },
        imgs: brand
      }
    },
    props: {
      direction: {
        type: String,
        default: 'horizontal'
      },
      height: {
        type: 'String',
      }
    },
}

</script>

<style lang="scss" scoped>
$bk-color: #e6e6e6;
.swiper-button-prev, .swiper-container-rtl .swiper-button-next{
  background-size: cover;
}
.swiper-button-prev, .swiper-button-next{
  width: 24px;
  height: 33px;
  background-color: #d4d4d4;
}
.swipe{
  height: 100%;
}
.swiper-pagination-bullet{
  width: 26px;
  height: 4px;
  border-radius: 0;
  background-color: $bk-color;
}
span{
  z-index: 100;
}
.bg-div {
  background-color: $bk-color;
  color: #fff;
  overflow: hidden;
  height: 100%;
}
.swiper-slide {
  width: 100%;
  float: left;
}
img{
  width: 80%;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
</style>



