<template lang="html">
  <div class="works">
      <div class="top-side">
        <div class="side-wrap">

          <div class="stack triple">
            <div class="square square-2">
              <div class="block">
                <router-view></router-view>
              </div>
            </div>
            <div class="square ho">
              <div class="block">
                <illusion bg="url(../mr/static/ho.jpg)" size="100% 133%"></illusion>
              </div>
            </div>
          </div>


          <div class="stack double">
            <div class="square square-85">
              <div class="block">
                <list></list>
              </div>
            </div>
            <div class="square square-28">
              <div class="block">
                <brand class="brand"></brand>
              </div>
            </div>
          </div>


          <div class="stack">
            <div class="square square-100">
              <div class="block">
                <honour class="honour"></honour>
              </div>
            </div>
          </div>

        </div>
      </div>
  </div>
</template>

<script>
import Gallery from '../components/Gallery'
import Illusion from '../components/Illusion'
import Brand from '../components/Brand'
import List from '../components/WorksList'
import Honour from '../components/Honour'


export default {
  name: 'about',
  components: {
    Gallery,
    Illusion,
    Brand,
    List,
    Honour
  },
}
</script>

<style lang="scss" scoped>
.about {
  width: 100%;
  max-width: 1500px;
  min-width: 1000px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  .top-side{
    padding: 0 0 37.5% 0;
    position: relative;
    .side-wrap{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
  }
}

.stack{
  width: 16.667%;
  float: left;
  display: block;
  position: relative;
  vertical-align: top;
  // border: 1px solid #000;
}
.triple{
  width: 50%;
}
.double{
  width: 33.334%;
}
@media (max-width: 768px) {
  .about{
    overflow: visible;
    min-width: inherit;
    .top-side{
      padding-bottom: 287%;
    }
  }
  .stack{
      width: 100%;
  }
  .triple{
    width: 100%;
  }
  .double{
    width: 100%;
  }
}
.square{
  display: block;
  position: relative;
  padding: 0 0 25% 0;
  .block{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-left: 1px solid #fff;
    overflow: hidden;
  }
  .block-border{
    left: 1px;
  }
}
.illusion{
  border-top: 1px solid #fff;
  height: 100%;
}
.square-2{
  padding: 0 0 50% 0;
  background-color: #556;
}
.square-28{
  padding-bottom: 28%;
}
.square-50{
  padding: 0 0 38% 0;
}
.top{
  margin-top: -1px;
}
.square-75{
  padding: 0 0 75.5% 0;
}
.square-85{
  padding-bottom: 85%;
}
.square-100{
  padding: 0 0 225% 0;
}
.square-113{
  padding-bottom: 113%;
  background-color: #556;
}
@media (max-width: 768px) {
  .square-85{
    padding-bottom: 80%;
  }
  .square-100{
    padding-bottom: 110%;
  }
  .square-113{
    padding-bottom: 60%;
  }
  .square .block{
    border-left: 0;
  }
  .square .block-border{
    left: 0;
  }
}
@media (max-width: 640px) {
  .ho{
    padding: 0;
  }
}
</style>



