<template lang="html">
  <section class="about">
    <div class="about-wrap">
      <nav class="marketing-list">
        <ul>
          <router-link v-for="link in links" :key="link.title" to="about" tag="li"><img :src="link.src" :alt="link.title"><p>{{link.title}}</p></router-link>
        </ul>
      </nav>
      <router-link to="/design">
        <figure>
          <h3>Create And Design</h3>
        </figure>
    </router-link>
    </div>
  </section>
</template>

<script>
export default {
  name: 'about',
  data(){
    return {
      links: [
        {src: "static/design.png", title: "文旅产品设计"},
        {src: "static/wechat.png", title: "公众号代理营销"},
        {src: "static/world.png", title: "国际文化交流"},
        {src: "static/web.png", title: "品牌网站创意"}
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
$bk-color: #a0c73c;
.about{
  float: left;
  overflow: hidden;
  width: 100%;
  height: 100%;
  .about-wrap{
    position: relative;
    background: #FFF;
    float: left;
    width: 100%;
    height: 100%;
    border-top: 1px solid rgba( 0, 0, 0, 0.1 );
    top: -1px;
  }
}
.marketing-list{
  width: 100%;
  height: 85%;
  float: left;
  ul{
    width: 100%;
    height: 100%;
    li{
      width: 50%;
      height: 50%;
      float: left;
      cursor: pointer;
      position: relative;
      background-color: $bk-color;
      border-bottom: 1px solid #f1f1f1;
      border-right: 1px solid #f1f1f1;
      &:nth-of-type(2n){
        border-right: none;
      }
      &:hover{
        background-image: -webkit-radial-gradient( 50% 50%, circle closest-corner, rgb(197,234,101), rgb(150,188,50));
      }
    }
  }
}
a{
  width: 100%;
  height: 15%;
  position: absolute;
  bottom: 0px;
  background: #000;
  // border-right: 1px solid #FFF;
  margin: 0;
  text-align: center;
  display: flex;
  justify-content: center;
}
figure{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  margin: 0;
  h3{
    color: #000;
    align-self: center;
    font-weight: 400;
    text-shadow: 0 1px #444;
  }
}
img{
  position: absolute;
  left: 50%;
  top: 50%;
  width: 42px;
  transform: translateX(-50%) translateY(-50%);
}
p{
  position: absolute;
  left: 50%;
  top: 85%;
  text-align: center;
  width: 112px;
  line-height: 22px;
  transform: translateX(-50%) translateY(-50%);
}
</style>



