<template lang="html">
  <div class="social">
    <div class="social-box">
      <ul>
        <li class="phone">
          <div>
            <i class="fa fa-phone fa-fw"></i>
            <a href="tel://+0471-2523513">
              0471-2523513
            </a>
          </div>
        </li>
        <li class="email">
          <div>
            <i class="fa fa-envelope fa-fw"></i>
            <a href="mailto:mrwh2017@163.com">
              mrwh2017@163.com
            </a>
          </div>
        </li>
        <li class="address">
          <div>
            <i class="fa fa-map-marker fa-fw"></i>
            <a>
              呼和浩特市天府花园 8 楼
            </a>
          </div>
        </li>
        <li class="wechat">
          <div>
            <i class="fa fa-wechat fa-fw"></i>
            <a v-on:mouseover="slide">
              + 加关注
            </a>
          </div>
        </li>
      </ul>
    </div>
    <div class="wechat-qr" v-on:mouseout="slideOut" style="backgroundImage: url(../mr/static/mrwh.png)">
    </div>
  </div>
</template>

<script>
export default {
  name: 'contact',
  methods: {
      slide(){
        var box = this.$el
        box.style.marginLeft = '-100%'
      },
      slideOut(){
        var box = this.$el
        box.style.marginLeft = '0';
      }
  }
}
</script>

<style lang="scss" scoped>
  a{
    font-size: 12px;
    color: #fff;
    cursor: pointer;
  }
  .social{
    width: 200%;
    height: 100%;
    background-color: #6c6c6c;
    opacity: 0.8;
    overflow: hidden;
    transition: margin-left 0.5s 0.2s ease-out, opacity 0.3s ease-in;
    &:hover{
      opacity: 1;
    }
    .social-box{
      width: 50%;
      height: 100%;
      float: left;
    }
    .wechat-qr{
      width: 50%;
      height: 100%;
      float: left;
      background:  no-repeat center;
      background-size: 60%;
    }
  }
  @media (max-width: 768px) {
    .social{
      transition: margin-left 0.5s ease-out;
    }
  }
  ul{
    position: absolute;
    width: 100%;
    height: 100%;
    float: left;
    color: #fff;
    li{
      float: left;
      border-bottom: 1px solid #fff;
      width: 100%;
      height: 25%;
      position: relative;
      display: flex;
      div{
        align-self: center;
        margin-left: 20px;
      }
    }
  }
  .wechat a{
    padding: 3px 30px 3px 25px;
    background-color: #62d647;
  }
</style>



