<template lang="html">
  <div class="contact">
      <div class="top-side">
        <div class="side-wrap">

          <div class="stack penta">
            <div class="square square-45">
              <div class="block">
                <div id="container" style="backgroundImage: url(../mr/static/map.jpg)"></div>
              </div>
            </div>
          </div>

          <div class="stack">
            <div class="square-113 square">
              <div class="block">
                <contact></contact>
              </div>
            </div>
            <div class="square square-113">
              <div class="block">
                <illusion class="illusion"></illusion>
              </div>
            </div>
          </div>


        </div>
      </div>
  </div>
</template>

<script>

// init();
import Contact from '../components/Contact'
import Gallery from '../components/Gallery'
import Illusion from '../components/Illusion'
import Brand from '../components/Brand'
import List from '../components/WorksList'

export default {
  name: 'about',
  mounted: function () {
    this.$nextTick(function () {
      var map = new AMap.Map('container',{
         zoom: 14,
         center: [111.6502976418,40.8477725238]
      });
      map.plugin(["AMap.ToolBar"], function() {
       map.addControl(new AMap.ToolBar());
      });

      addMarker();
       function addMarker() {
           map.clearMap();
           var marker = new AMap.Marker({
               map: map,
               position: [111.6502976418,40.8477725238]
           });
           //鼠标点击marker弹出自定义的信息窗体
           AMap.event.addListener(marker, 'click', function() {
               infoWindow.open(map, marker.getPosition());
           });
       }


      var title = '<p>蒙荣文化发展有限公司</p>';
      var content=[];
        content.push("<p>地址：通道北路天府花园 2 号商务楼 811</p>");
        content.push("电话：0471 - 2523513");
        // content.push("<a href="http://ditu.amap.com/detail/B000A8URXB?citycode=110105" target="_blank">详细信息</a>");
        var infoWindow = new AMap.InfoWindow({
            isCustom: true,  //使用自定义窗体
            content: createInfoWindow(title,
              content.join("")),
            offset: new AMap.Pixel(16, -50)//-113, -140
        });
        infoWindow.open(map, [111.6502976418,40.8477725238]);

      function createInfoWindow(title, content) {
       var info = document.createElement("div");
       info.className = "info";

       //可以通过下面的方式修改自定义窗体的宽高
       info.style.width = "330px";
       info.style.backgroundColor = "rgba(0, 0, 0, 0.59)";
       info.style.padding = "10px 20px";
       info.style.color = "#e6e6e6";
       // 定义顶部标题
       var top = document.createElement("div");
       var titleD = document.createElement("div");
       var closeX = document.createElement("div");
       top.className = "info-top";
       titleD.innerHTML = title;
       closeX.innerHTML = "X";
       closeX.style.top = "6px";
       closeX.style.right = "10px";
       closeX.style.position = "absolute";
       closeX.onclick = closeInfoWindow;

       top.appendChild(titleD);
       top.appendChild(closeX);
       info.appendChild(top);

       // 定义中部内容
       var middle = document.createElement("div");
       middle.className = "info-middle";
      //  middle.style.backgroundColor = 'white';
       middle.style.fontSize = '14px';
       middle.style.color = '#d6d6d6';
       middle.style.marginTop = "12px";
       middle.innerHTML = content;
       info.appendChild(middle);

       return info;
   }

     function closeInfoWindow() {
         map.clearInfoWindow();
     }

    })
  },
  components: {
    Gallery,
    Illusion,
    Brand,
    List,
    Contact
  }
}
</script>

<style lang="scss" scoped>
#container{
  width: 100%;
  height: 100%;
  background: url(../mr/static/map.jpg) no-repeat center;
  background-size: cover;
}
.contact{
  width: 100%;
  max-width: 1500px;
  min-width: 1000px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  .top-side{
    padding: 0 0 37.5% 0;
    position: relative;
    .side-wrap{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
  }
}

.stack{
  width: 16.667%;
  float: left;
  display: block;
  position: relative;
  vertical-align: top;
  // border: 1px solid #000;
}
.penta{
  width: 83.334%;
}
.triple{
  width: 50%;
}
.double{
  width: 33.334%;
}
@media (max-width: 768px) {
  .contact{
    overflow: visible;
    min-width: inherit;
    .top-side{
      // padding-bottom: 287%;
    }
  }
  .stack{
    width: 100%;
  }
  .triple{
    width: 100%;
  }
  .double{
    width: 100%;
  }
  .penta{
    width: 100%;
  }
}

.square{
  display: block;
  position: relative;
  padding: 0 0 25% 0;
  .block{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-left: 1px solid #fff;
    overflow: hidden;
  }
  .block-border{
    left: 1px;
  }
}
.illusion{
  border-top: 1px solid #fff;
}
.square-2{
  padding: 0 0 50% 0;
}
.square-28{
  padding-bottom: 28%;
}
.square-50{
  padding: 0 0 38% 0;
}
.top{
  margin-top: -1px;
}
.square-45{
  padding: 0 0 45% 0;
}
.square-75{
  padding: 0 0 75.5% 0;
}
.square-85{
  padding-bottom: 85%;
}
.square-100{
  padding: 0 0 225% 0;
}
.square-113{
  padding-bottom: 113%;
}
@media (max-width: 768px) {
  .square-45{
    padding-bottom: 80%;
  }
  .square-100{
    padding-bottom: 100%;
  }
  .square-113{
    padding-bottom: 60%;
  }
  .square .block{
    border-left: 0;
  }
  .square .block-border{
    left: 0;
  }
}
</style>



