<template lang="html">
    <section class="info">
      <gallery class="gallery" :imgs="imgs[$route.params.id]"></gallery>
    </section>
</template>

<script>
import {gallery, ejn, als, dmcg, zld, hunu} from '../api/api';
import Gallery from './Gallery'
// console.log(vm.gallery);

export default {
  name: 'WorksInfo',
  components: {
    Gallery
  },
  data: function(){
    return {
      imgs: {
        gallery: gallery,
        ejn: ejn,
        als: als,
        dmcg: dmcg,
        zld: zld,
        hunu: hunu
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.info, .gallery{
  width: 100%;
  height: 100%;
}
</style>



