import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '@/components/NotFound'
import Header from '@/components/Illusion'
import Home from '@/views/home'
import About from '@/views/about'
import Works from '@/views/works'
import WorksInfo from '@/components/WorksInfo'
import Contact from '@/views/contact'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: '蒙荣文化 · 内蒙古民族文化发扬者'
      }
    },
    {
      path: '/About',
      name: 'About',
      component: About,
      meta: {
        title: '蒙荣文化 · 关于我们'
      }
    },
    {
      path: '/works',
      component: Works,
      meta: {
        title: '蒙荣文化 · 案例与荣誉展示'
      },
      children: [
        {
          path: '',
          name: 'fd',
          component: WorksInfo,
          meta: {
            title: '蒙荣文化 · 案例与荣誉展示'
          }
        },
        {
          path: ':id',
          component: WorksInfo,
          name: 'worksinfo',
          meta: {
            title: '蒙荣文化 · 案例与荣誉展示'
          }
        }
      ]
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact,
      meta: {
        title: '蒙荣文化 · 联系方式'
      }
    },
    {
      path: '*',
      name: 'NotFound',
      component: NotFound,
      meta: {
        title: '蒙荣文化 · 你要访问的页面已经挂掉了'
      }
    }
  ]
})



