<template lang="html">
  <div class="swipe">
    <swiper :options="swiperOption" ref="mySwiper" class="bg-div mySwiper" :style="{height: height}">
      <swiper-slide v-for="(i,index) in imgs" :key="i"><img :src="i" alt="" ></swiper-slide>

      <div class="swiper-pagination"  slot="pagination"></div>
      <!-- <div class="swiper-button-prev" slot="button-prev"></div>
      <div class="swiper-button-next" slot="button-next"></div> -->
    </swiper>
  </div>
</template>

<script>
import {gallery} from '../api/api'

export default {
  name: 'Gallery',
  data() {
      return {
        swiperOption: {
          control: 'mySwiper',
          notNextTick: true,
          autoplay: 3000,
          speed: 500,
          loop: true,
          effect : 'slide',
          direction : this.direction,
          grabCursor : true,
          setWrapperSize :true,
          autoHeight: false,
          pagination : '.swiper-pagination',
          paginationClickable :true,
          prevButton:'.swiper-button-preva',
          nextButton:'.swiper-button-nexta',
          // scrollbar:'.swiper-scrollbar',
          mousewheelControl : true,
          observeParents:true,
          debugger: true,
          observer:true,
          // swiper callbacks
          // swiper的各种回调函数也可以出现在这个对象中，和swiper官方一样
          // onTransitionStart(swiper){
          //   console.log(swiper)
          // },
        }
      }
    },
    props: {
      imgs: {
        default: function(){
          return gallery
        }
      },
      direction: {
        type: String,
        default: 'horizontal'
      },
      height: {
        type: 'String',
      }
    },
}

</script>

<style lang="scss" scoped>
$bk-color: #000;

span{
  z-index: 100;
}
.bg-div {
  background-color: $bk-color;
  color: #fff;
  overflow: hidden;
  height: 100%;
}
.swiper-slide {
  width: 100%;
  float: left;
}
img{
  width: 100%;
}
</style>



